/* Global */

* {
     font-family: Avenir, -apple-system, BlinkMacSystemFont, Segoe UI, Roboto,
          Helvetica Neue, Arial, Noto Sans, sans-serif, Apple Color Emoji,
          Segoe UI Emoji, Segoe UI Symbol, Noto Color Emoji;
}

/* Chats Page */

.chats-page {
     position: absolute;
     top: 0px;
     left: 0px;
     width: 100vw;
     height: 100vh;
}
.logo-img-tab {
     width: 50px;
     height: 50px;
}
.nav-bar {
     width: 100%;
     height: 66px;
     display: flex;
     justify-content: center;
     align-items: center;
     background-color: #007575;
}

.logo-tab {
     position: absolute;
     left: 50px;
     top: 12px;
     font-size: 24px;
     font-weight: 700;
     color: #a7f2ff;
}
.chat__logo {
     width: 50px;
     height: 50px;
     position: absolute;
     left: 0;
     top: 4px;
}
.logout-tab {
     position: absolute;
     top: 22px;
     right: 22px;
     color: #a7f2ff;
     cursor: pointer;
}

::-webkit-scrollbar {
     width: 0; /* Remove scrollbar space */
     background: transparent; /* Optional: just make scrollbar invisible */
}

/* Login Page */

#login-page {
     background-image: linear-gradient(#005353, #09d9d9);
     position: absolute;
     top: 0px;
     bottom: 0px;
     left: 0px;
     right: 0px;
     display: flex;
     justify-content: center;
     align-items: center;
}
.login__logo {
     position: absolute;
     width: 170%;
     height: auto;
     z-index: -1;
     opacity: 0.2;
}
#login-card {
     margin-top: -10px;
     position: relative;
     width: 480px;
     height: 400px;
     display: flex;
     align-items: center;
     justify-content: center;
     flex-direction: column;
     text-align: center;
     background-color: rgba(148, 239, 255, 0.699);
     backdrop-filter: blur(20px);
     border-radius: 22px;
     z-index: 1 !important;
}

.login-button {
     margin-top: -10px;
     cursor: pointer;
     color: white;
     padding: 12px;
     border-radius: 8px;
     display: inline-block;
}
.login__title {
     color: rgb(17, 103, 109);
     margin-top: -10px;
     text-shadow: 1px 1px rgb(0, 197, 231);
     font-size: 30px;
     line-height: 2;
}
.facebook {
     background-color: #646464;
     min-width: 200px;
     pointer-events: none;
}
#ce-options-drop-down,
.ce-chat-form-container,
.ce-section-title-container {
     color: #1b535a;
}
#new-chat-plus-button,
#ce-send-message-button {
     background-color: #14aea8 !important;
}

.google {
     background-color: #296b74;
     color: white;
     min-width: 200px;
     margin-top: 40px;
}

#loading-container {
     position: absolute;
     top: 0px;
     bottom: 0px;
     left: 0px;
     right: 0px;
     background-color: rgb(255, 255, 255, 0.22);
}

#loading-icon {
     font-size: 66px;
     position: relative;
     left: calc(50vw - 33px);
     top: calc(50vh - 22px);
}
